module.exports = "/**\n *\n * Hey. My name's Jason Wong. I'm a technical geek and an occasional freelance contractor.\n *\n * This is my dynamic resume show case.\n */\n\n/**\n * Let's begin. We start by animating... well, everything.\n */\n* {\n  -webkit-transition: all .3s;\n  transition: all .3s;\n}\n\n/**\n * That didn't do much. But you'll see.\n *\n * Black on white is really boring,\n * so let's do something about it.\n */\nhtml {\n  color: rgb(222,222,222);\n  background: rgb(63, 82, 99);\n}\n\n/* Let's make the font better. */\nbody * {\n  font-family: \"SourceCodePro\", \"Microsoft YaHei\", \"微软雅黑\", \"Helvetica Neue\", Helvetica, sans-serif;\n}\n\n/**\n * That's better. Sorry about your eyes.\n *\n * Working in this big empty space is tough.\n *\n * I'm going to make a nice area for us to work in.\n */\n.styleEditor {\n  overflow: auto;\n  background: rgb(48, 48, 48);\n  border: 1px solid #ccc;\n  border-radius: 6px;\n  font-size: 16px;\n  box-shadow: -4px 4px 2px 0 rgba(0,0,0,0.3);\n  padding: .5em;\n  margin: .5em;\n  width: 48vw; height: 98vh;\n  -webkit-transform: translateX(100%);\n  position: fixed; right: 48%; top: 0;\n}\n\n/**\n * Make it a bit tridimensional.\n */\n\n.styleEditor pre {\n  white-space: pre-wrap;\n  outline: 0;\n}\n\nbody {\n  -webkit-perspective: 1024px;\n}\n\n.styleEditor {\n  -webkit-transform: translateX(100%) rotateY(-8deg);\n  -webkit-transform-origin: right;\n}\n\n/**\n * This is good, but all the text is white!\n * Let's make it even more readable.\n */\n.token.selector{ color: rgb(166, 226, 46); }\n.token.property{ color: rgb(102, 217, 239); }\n.token.punctuation{ color: yellow; margin-left: 2px; }\n.token.function{ color: rgb(249, 38, 114); }\n\n/**\n * So, let's see my resume. That's why you're here, right?\n * I can't imagine you'd come just to see the pretty colors. ;-)\n */\n.resumeEditor{\n  font-size: 16px;\n  position: fixed; left: 0; top: 0;\n  padding: .5em;\n  margin: .5em;\n  box-shadow: -4px 4px 2px 0 rgba(0,0,0,0.3);\n  width: 51vw; height: 98vh;\n  border: 1px solid #ccc;\n  border-radius: 6px;\n  /* background: rgb(48, 48, 48); */\n  background: white;\n  color: #222;\n  overflow: auto;\n  -webkit-transform: translateX(0%) rotateY(8deg);\n  -webkit-transform-origin: left;\n  white-space: pre-wrap;\n  outline: 0;\n}\n\n/* OK, now let me write the resume... */\n"

